  body, h1, p{
    background-color: hsl(1, 100%, 74%);
    background-image: url("bg-intro-desktop.png");
    margin: 0px;
    padding: 0px;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
  }
  .main{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
    "Contents Buttons"
    "Contents forms";
    grid-column-gap: 55px;
    justify-content: center;
    align-content: center;
    place-items: center;
  }
  .section1{      
    grid-area: Contents;
    text-align: left;
    margin: 0;
    padding: 0px 0px 0px 50px;
  }
  h1{
    font-weight: 700;
    font-size: 29px;
    font-family: 'Poppins', sans-serif;
    word-spacing: 2px;
    letter-spacing: .7px;
  }
  p{
    margin-top: 15px;
    padding: 0px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    word-spacing: .1px;
  }
  .section2{
    background-color: hsl(248, 32%, 49%);
    color: #fff;
    grid-area: Buttons;
    padding: 0px;
    width: 700px;
    font-size: 16px;
    font-weight: 400;
    box-shadow: 0px 4px rgba(128, 128, 128, 0.384);
    border: none;
    border-radius: 5px;
    height: 55px;
    padding: 10px;
    position: relative;
    top: 79px;
    right: 29px;
    bottom: 0px;
    margin: 0 auto;
  }
  form{
    grid-area: forms;
    place-content: center center;
    max-width: 700px;
    min-width: 390px;
    width: 100%;
    background-color: #fff;
    border: 0px;
    border-radius: 5px;
    box-shadow: 0px 4px rgba(128, 128, 128, 0.384);
    padding-bottom: 20px;
    margin-right: 60px; 
    position: relative;
    bottom: 60px;
  }
  input{
    margin-left: 14px;
    margin-top: 15px;
    border: 1px solid rgba(128, 128, 128, 0.336);
    color: hsla(249, 10%, 26%, 0.849);
    border-radius: 4px;
    width: 90%;
    padding-left: 19px;
    height: 50px;
  
  }
  form button{
    width: 95%;
    height: 50px;
    margin-left: 14px;
    margin-top: 15px;
    padding-top: 5px;
    color: #fff;
    background-color: hsl(154, 59%, 51%);
    border: none;
    border-bottom: rgba(128, 128, 128, 0.384) solid 2px;
    border-radius: 4px;
  }